import { Component } from '@angular/core';

@Component({
  selector: "app-root",
  template: require('./app.component.html'),
  styles: [require('./app.component.css')]
})

export class AppComponent{
  emoji = [":(", ":)", "XD"];
  activeEmoji: string ;

  changeEmoji(){
    this.activeEmoji = this.emoji[Math.floor(Math.random() * this.emoji.length)];
  }



}



